.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  display: flex;
}

.header__ul {
  margin: 0;
  padding: 0;
  display: flex;
}

.header__li {
  margin-left: 20px;
  list-style: none;
}

.header__li:first-child {
  margin-left: 0;
}

.header__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.header__link.is-active {
  color: #00acdc;
}

.header__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.header__link:hover {
